import { Component, Input, OnInit } from '@angular/core';
import { CalendarComponentOptions } from 'ion2-calendar';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from "@angular/forms";

import { Customer } from '../interface/customerInferface';
import { CustomerService } from '../customer.service';

@Component({
  selector: 'app-home',
  templateUrl: 'newsignup.component.html',
  styleUrls: ['newsignup.component.css'],
})

export class NewsignupComponent implements OnInit {

  @Input()
  customer: Customer;
  
  calendarDate: string;
  type: 'string';
  customers: Customer[] = [];

  customersForm = new FormGroup({
    username: new FormControl(''),
  })

  constructor(private customersService: CustomerService) {}

  ngOnInit() {

  }

  onChange($event) {
    console.log($event);
    var s = new Date($event).toLocaleDateString("en-US")
  }

  backToHome(): void{
    this.customersForm.reset();
    console.log("zurück zum Menü");
  }

  submit(customer: Customer): void{
    customer.greenSmile=0;
    customer.redSmile=0;
    this.customersService.saveCustomer(customer);
    this.customersForm.reset();
    console.log("Eintrag gespeichert")
  }

  loadCustomer(id: number) {
    this.customersService.getCustomer(id)
    console.log("die id ist:" + id)
  }
}